.navbar_container {
    position: fixed;
    width: 100%;
    bottom: 0%;
    left: 0;
    display: flex;
    z-index: 2;
}

.Dark {
    background-color: rgb(38, 38, 38);
}
.Light {
    background-color: white;
}
.navbar_container ul {
    width: 100%;
    padding: 5px 10px;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}