.blank_shift_container{
    display: flex;
    flex-direction: column;
    line-height: 2;
    font-size: 20px;
    padding: 5px;
}
.single_product{
    margin-bottom: 3px;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.ready_product{
    margin-bottom: 3px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#setDoneBtn{
    width: 4rem;
    color: rgb(227, 63, 52);
    text-align: center;
    cursor: pointer;
    font-size: medium;
    padding: 2px;
    border: 1px solid white;
    border-radius: 5px;

}
.controls{
    display: flex;
    justify-content: space-between;
    padding: 7px 0px;
}
.shift_controls {
    
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    /* border-bottom: 1px solid orange; */
    margin-bottom: 5px;
}
.shift_controls span {
    border-radius:10px;
    width: 50%;
    text-align: center;
    cursor: pointer;
}
.active{
    background-color: rgb(88, 51, 161);
}

.controls img {
    width: 30px;
    height: 30px;
}
.blank_shift_container img {
    padding: 0px 10px;
}
.checked_element{
    background-color: orange;
}
.unchecked_element {
    background-color : black
}

.single_blank_container{
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: max-content;
    /* background-color:rgb(33, 33, 33); */
    border-radius: 5px;
    border: 1px solid white;
}
.single_blank_container a {
    color: rgb(253, 60, 60);
}
.nav_link{
    align-items: flex-end;
    justify-self: flex-end;
    position: fixed;
    bottom: 4rem;
    right: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    border-radius: 180px;
    background-color: rgb(34, 34, 34);
    border: 1px solid white;
}
/* Current SHIFT */
.progress_bar{
    display: flex;
    justify-content: space-between;
}

/* EMPTY SHIFT */

.empty_shift_container{
    height: 80vh;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.empty_shift_container h1 {
    columns: orangered;
}

#EndShift{
    color: white;
    border-radius: 5px;
    font-size: 20px;
    width: 50%;
    position: fixed;
    bottom: 4rem;
    right: 25%;
    background-color: rgb(41, 41, 41);
    height: 40px;
}

#EndShift:hover {
    background-color: white;
    border: 1px solid orangered;
    color: black;
}
#showMore{
    color: rgb(242, 59, 59);
    cursor: pointer;
}

.history_container{

    overflow-y: scroll;
    
}

.blank_shift_container{
    animation: trans 0.4s forwards;
}

@keyframes trans {
    0%{
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0%);
    }
}
.blank_shift_blanks_container{
    animation: trans 0.4s forwards;
}


@keyframes trans {
    0%{
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0%);
    }
}